*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: translateX(-50%) translateY(-50%) scale(0) rotate(0deg) skew(40deg, 40deg);
}

.is-hidden .buy-now-modal-body {
  scale: 0;
}

.modal:not(.is-hidden) .buy-now-modal-body {
  scale: 1;
}

.buy-now-modal {
  position: fixed;
  inset: 0;
  background-color: rgb(0 0 0 / 0.5);
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  transition: opacity 250ms;
  margin: 0 auto;
  overflow: auto;
  z-index: 9;
}

.buy-now-modal_body {
  position: relative;
  max-width: 280px;
  margin: auto;
  background-color: var(--light-bg-font-color);
  border: 2px solid var(--default-btn-color);
  border-radius: 10px;
  top: 60px;
}

@media screen and (min-width: 768px) {
  .buy-now-modal_body {
    max-width: 568px;
  }
}

.buy-now-modal_body h3 {
  text-align: center;
  margin-top: 28px;
  font-family: Seymour One;
  font-size: 16px;
